*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.coin-app{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: azure;
}

.coin-search{
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.coin-text{
    margin-bottom: 32px;
    text-align: center;
}
.coin-input{
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 5px;
    border: none;
}
.coin-input::placeholder{
    color: rgb(0, 81, 255);
}