.coin-container{
    display: flex;
    justify-content: center;
}
.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7d7;
    width: 900px;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
}
.coin-symbol{
    text-transform: uppercase;
}
.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}
.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coin-price{
    margin-top: 10px;
    width: 110px;
}
.coin-volume{
    margin: 10px;
    width: 230px;
}
.coin-marketCap{
    margin-top: 10px;
    width: 250px;
}
.coin-percent{
    margin-top: 10px;
    width: 50px;
}
.red {
    color: red;
}
.green{
    color: rgb(16, 236, 16);
}